import {List, ListItemSize, ListPadding, ListSelectPayload, ListVariant} from '@here/hds-list';
import React, {useEffect, useImperativeHandle, useRef} from 'react';

// eslint-disable-next-line no-new
new List();
export interface HDSListProperties
  extends Omit<React.HTMLAttributes<HTMLDivElement>, 'onSelect' | 'unselectable'> {
  unhoverable?: boolean;
  unpressable?: boolean;
  unfocusable?: boolean;
  unselectable?: boolean;
  disabled?: boolean;
  innerRole?: string;
  innerAriaLabel?: string;
  truncate?: boolean;
  groupTitle?: string;
  width?: string;
  selectedIndex?: number;
  variant?: ListVariant;
  size?: ListItemSize;
  horizontalPadding?: ListPadding;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any, no-unused-vars
  onSelect?: (index: ListSelectPayload) => void;
}

const HDSList = React.forwardRef((props: HDSListProperties, ref) => {
  const {
    unhoverable,
    unpressable,
    unfocusable,
    unselectable,
    disabled,
    onSelect,
    groupTitle,
    innerAriaLabel,
    innerRole,
    selectedIndex,
    variant,
    size,
    horizontalPadding,
    className,
    children,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<ListSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', _handleSelected as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);

  return (
    <hds-list
      {...(unhoverable && {unhoverable})}
      {...(unpressable && {unpressable})}
      {...(unfocusable && {unfocusable})}
      {...(unselectable && {unselectable})}
      {...(disabled && {disabled})}
      {...(groupTitle && {'group-title': groupTitle})}
      {...(innerAriaLabel && {'inner-aria-label': innerAriaLabel})}
      {...(innerRole && {'inner-role': innerRole})}
      {...(selectedIndex && {'selected-index': selectedIndex})}
      {...(horizontalPadding && {'horizontal-padding': horizontalPadding})}
      {...(variant && {variant})}
      {...(size && {size})}
      {...(className && {class: className})}
      {...rest}
      ref={innerRef}
    >
      {children}
    </hds-list>
  );
});

HDSList.defaultProps = {
  innerRole: undefined,
  innerAriaLabel: undefined,
  truncate: true,
  groupTitle: '',
  variant: 'subtle',
  size: 'small',
  horizontalPadding: 'default',
  selectedIndex: -1,
  disabled: false,
  unhoverable: false,
  unpressable: false,
  unfocusable: false,
  unselectable: false,
};

export default HDSList;
